import userModel from "@/model/user"
  export default {
      name: "Login",
      data()
      {
        return {
          username:"",
          password:"",
          repassword:"",
          code:"",
          email:"",
          time:3,
          remaining:3,
          code_tip:"获取验证码",
          inTime:true,//是否到点
        }
      },
      methods:
      {
        //返回
        goback:function()
        {
          this.$router.back(-1)
        },
        //倒计时
        countDown:function()
        {
          var that=this
          this.inTime=false
          var coutDownTimeStamp=setInterval(function()
          {
            that.code_tip="剩余"+that.remaining+"秒"
            if(that.remaining===0)
            {
              clearInterval(coutDownTimeStamp)
              that.code_tip="获取验证码"
              that.remaining=that.time+1
              that.inTime=true
            }
            that.remaining--
          },1000)
        },
        //获取验证码
        getCode:function()
        {
          if(this.inTime)
          {

            if(!this.email)
            {
              this.$vux.toast.show({
                text: '请输入邮箱',
                type:'text',
                position:"bottom"
              });
              return;
            }
            if(!(/^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/.test(this.email)))
            {
              this.$vux.toast.show({
                text: '邮箱格式错误',
                type:'text',
                position:"bottom"
              });
              return;
            }
            var that=this;
            userModel.getCode(this.email,function(ret)
            {
              if(ret&&ret.isSuccess)
              {
                //倒数开始
                that.countDown();
              }
              if(ret&&ret.message)
              {
                that.$vux.toast.show({
                  text: ret.message,
                  type:'text',
                  position:"bottom"
                });
              }

            });
          }
        },
        //注册
        register:function()
         {

            var that=this,
            param={
              username:this.username,
              password:this.password,
              password2:this.repassword,
              code:this.code,
              email:this.email
            };
           if(!that.username)
           {
             that.$vux.toast.show({
               text: '请输入用户名',
               type:'text',
               position:"bottom"
             });
             return;
           }
           if(!that.password)
           {
             that.$vux.toast.show({
               text: '请输入密码',
               type:'text',
               position:"bottom"
             });
             return;
           }

           if(!that.repassword)
           {
             that.$vux.toast.show({
               text: '请输入确认密码',
               type:'text',
               position:"bottom"
             });
             return;
           }
           if(that.password!==that.repassword)
           {
             that.$vux.toast.show({
               text: '两次密码不一致',
               type:'text',
               position:"bottom"
             });
             return;
           }
           if(!that.code)
           {
             that.$vux.toast.show({
               text: '请输入验证码',
               type:'text',
               position:"bottom"
             });
             return;
           }
           if(!that.email)
           {
             that.$vux.toast.show({
               text: '请输入邮箱',
               type:'text',
               position:"bottom"
             });
             return;
           }
           if(!(/^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/.test(that.email)))
           {
             that.$vux.toast.show({
               text: '请输入正确的邮箱',
               type:'text',
               position:"bottom"
             });
             return;
           }
           that.$vux.loading.show({
             text: '注册中，请稍等'
           })
            userModel.register(param,function(ret,err)
            {
              that.$vux.loading.hide()
               if(ret&&ret.message)
               {
                 that.$vux.toast.show({
                   text: ret.message,
                   type:'text',
                   position:"bottom"
                 });
               }
              if(ret&&ret.isSuccess)
              {
                setTimeout(function(){
                  that.$router.push("/login")
                },500)
              }
            })
         }
      },
      created()
      {

      }
  }



